.slider {
  position: absolute;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image {
  background-image: url(SliderData.js);
  background-size: cover;
  width: 100vw;
  height: 120vh;
  background-position: center;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}
.slide.active {
  opacity: 1;
  transition-duration: 0.3s;
}
@media screen and (max-width: 375px) {
  .image {
    background-size: cover;
  }
}
